<template>
  <div class="account-wrapper">
    <div class="sidebar-left">
      <el-scrollbar class="sidebar">
        <div class="item user-portrait">
          <el-image
            :src="
              user.associated
                ? user.associated.officia_account_of_wechat.avatar_url
                : 'url'
            "
            style="
              height: 100px;
              width: 100px;
              line-height: 100px;
              border-radius: 50%;
            "
          >
            <div slot="error" class="image-slot">
              <el-image src="../../../../static/image/pc/tx.png"></el-image>
            </div>
          </el-image>
          <div class="font">{{ user.user_name }}</div>
        </div>
        <div class="item">个人资料</div>
        <div class="item">账户安全</div>
      </el-scrollbar>
    </div>
    <div class="sidebar-right">
      <el-scrollbar class="sidebar">
        <div class="user-info">
          <div class="portrait item">
            <el-image
              :src="
                user.associated
                  ? user.associated.officia_account_of_wechat.avatar_url
                  : 'url'
              "
              style="
                height: 65px;
                width: 65px;
                line-height: 65px;
                border-radius: 50%;
              "
            >
              <div slot="error" class="image-slot">
                <el-image src="../../../../static/image/pc/tx.png"></el-image>
              </div>
            </el-image>
          </div>
          <div class="textInfo item">
            <div class="item-title">
              <div class="sidebar-title">基本信息</div>
            </div>
            <ul class="content-wrapper">
              <li class="item-wrapper">
                <div class="label">用户名</div>
                <div class="text">{{ user.user_name }}</div>
                <!-- <el-input v-show="isName" class="text" placeholder="请输入用户名" v-model="user.user_name" clearable></el-input> -->
                <i @click="open" class="el-icon-edit-outline"></i>
              </li>
              <li class="item-wrapper">
                <div class="label">用户编号</div>
                <div class="text">{{ user.uid }}</div>
              </li>
            </ul>
          </div>
        </div>
        <div class="user-securityInfo">
          <div class="item-title">
            <div class="sidebar-title">安全信息</div>
          </div>
          <ul class="content-wrapper security">
            <li class="item-wrapper">
              <div class="item-label">
                <div class="label">手机号</div>
                <div class="text">{{ user.phone_num }}</div>
              </div>
              <el-button
                type="warning"
                size="small"
                @click="$router.push('update_phone')"
                >修改</el-button
              >
            </li>
            <!-- <li class="item-wrapper">
              <div class="item-label">
                <div class="label">邮箱账号</div>
                <div class="text">{{user.email||'xxxx@xxx.com'}}</div>
              </div>
              <el-button
                type="warning"
                size="small"
                v-if="user.email"
                @click="$router.push('email')"
              >修改</el-button>
              <el-button size="small" v-else @click="$router.push('email')">未设置</el-button>
            </li>-->
            <li class="item-wrapper">
              <div class="item-label">
                <div class="label">登录密码</div>
                <div class="text">********</div>
              </div>
              <el-button
                size="small"
                v-if="user.note.pass_set_status"
                @click="$router.push('password')"
                >设置</el-button
              >
              <el-button
                type="warning"
                size="small"
                v-else
                @click="$router.push('update_pwd')"
                >修改</el-button
              >
            </li>
          </ul>
          <ul class="third-party">
            <li class="item-wrapper">
              <div class="flex-item">
                <div class="iconfont-">
                  <div class="icon weixin">
                    <i class="icon-48-38-60-47 iconfont"></i>
                  </div>
                  <div class="label">微信</div>
                </div>
                <div class="text">
                  {{
                    user.associated
                      ? user.associated.officia_account_of_wechat.nickname
                      : "未绑定"
                  }}
                </div>
              </div>
              <el-button
                size="small"
                v-show="user.associated"
                @click="removebind"
                >解绑</el-button
              >
              <el-button size="small" v-show="!user.associated">绑定</el-button>
            </li>
            <!-- <li class="item-wrapper">
              <div class="flex-item">
                <div class="iconfont-">
                  <div class="icon taobao">
                    <i class="icon-48-38 iconfont"></i>
                  </div>
                  <div class="label">支付宝</div>
                </div>
                <div class="text">未绑定</div>
              </div>
              <el-button class="text_button" type="warning" size="small">未绑定</el-button>
            </li>-->
          </ul>
        </div>
      </el-scrollbar>
    </div>
  </div>
</template>

<script>
import Nav from "@/components/Nav";
import { userInfo, userName, removeBind } from "@/api/user";
export default {
  components: {
    Nav,
  },
  props: {},
  data() {
    return {
      activeName: "personal",
      user: {
        note: {},
      },
    };
  },
  watch: {},
  computed: {},
  methods: {
    async getUserInfo() {
      let { data } = await userInfo();
      this.user = data;
    },
    updateName() {
      // this.isName=!this.isName
    },
    async removebind() {
      let _this = this;
      this.$confirm(`此操作将解除微信绑定, 是否继续?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(async () => {
          await removeBind({ scheme: 1 });
          _this.getUserInfo();
          _this.$message({
            type: "success",
            message: "解除成功!",
          });
        })
        .catch(() => {
          _this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    open() {
      this.$prompt("请输入用户名", "用户名", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputPattern: /^[a-zA-Z0-9_-]{4,16}$/,
        inputValue: this.user.user_name,
        inputErrorMessage: "用户名不符合规则(4到16位,字母，数字，下划线，减号)",
      })
        .then(async ({ value }) => {
          await userName({ user_name: value });
          this.getUserInfo();
          this.$message({
            type: "success",
            message: "你的是新用户名: " + value,
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消修改",
          });
        });
    },
  },
  created() {
    this.$emit("customStyle", 3);
    if (this.$route.query.name) {
      this.activeName = this.$route.query.name;
    }
    this.$nextTick(() => {
      this.getUserInfo();
    });
  },
  mounted() {},
};
</script>
<style lang="scss" scoped>
@import "../../../styles/global";
.account-wrapper {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding-top: 15px;
  box-sizing: border-box;
  background-color: #eee;
  /deep/ .sidebar {
    background-color: #fff;
    height: 100%;
    .el-scrollbar__wrap {
      overflow-x: hidden;
      .el-timeline-item:last-of-type {
        .el-timeline-item__tail {
          display: none;
        }
      }
    }
  }
  .sidebar-title {
    display: inline-block;
    font-size: 18px;
    text-indent: 1px;
    letter-spacing: 1px;
    padding-left: 5px;
    margin: -2px 0;
    color: #333333;
    border-left: 5px solid #e5b467;
  }
  .sidebar-left {
    width: 350px;
    max-height: 812px;
    margin-right: 30px;
    float: left;
    .item {
      position: relative;
      display: flex;
      width: 100%;
      height: 80px;
      align-items: center;
      justify-content: center;
      box-sizing: border-box;
      text-indent: 1px;
      letter-spacing: 1px;
      color: #999999;
      font-size: 18px;
      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 1px;
        background-color: #e5e5e5;
      }
    }
    .user-portrait {
      height: 250px;
      flex-direction: column;
      .font {
        font-size: 16px;
        margin-top: 20px;
        color: #333333;
      }
    }
  }
  .sidebar-right {
    width: 1000px;
    max-height: 812px;
    float: left;
    .content-wrapper {
      display: inline-block;
      .item-wrapper {
        width: 100%;
        display: flex;
        align-items: center;
        margin-block-end: 1.5em;
        .label {
          width: 4em;
          text-align: left;
          margin-right: 15px;
          color: #999999;
        }
        .text {
          color: #333333;
        }
      }
    }
    .user-info {
      position: relative;
      width: 100%;
      min-height: 250px;
      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 1px;
        background-color: #e5e5e5;
      }
      .item {
        display: flex;
        width: 500px;
        margin: 0 auto;
      }
      .portrait {
        height: 120px;
        justify-content: center;
        align-items: center;
      }
      .textInfo {
        display: flex;
        .item-title {
          margin-right: 80px;
        }
      }
      .text {
        width: 180px;
      }
      i {
        font-size: 20px;
        color: #e5b467;
        cursor: pointer;
      }
    }
    .user-securityInfo {
      width: 500px;
      padding: 30px 0;
      margin: 0 auto;
      .item-title {
        width: 100%;
        margin-block-end: 2em;
      }
      .el-button {
        width: 68px;
      }
      .security {
        width: 100%;
        margin-block-end: 2em;
        .item-label {
          flex-grow: 1;
          display: flex;
          align-items: baseline;
          .label {
            width: 5em;
            margin-right: 25px;
          }
        }
      }
      .third-party {
        width: 100%;
        height: auto;
        .item-wrapper {
          width: 100%;
          display: flex;
          align-items: center;
          font-size: 17px;
          margin-block-end: 1.5em;
          // padding-left: 45px;
          box-sizing: border-box;
          .flex-item {
            flex-grow: 1;
            display: flex;
            align-items: center;
            .iconfont- {
              width: 9em;
              display: inline-flex;
              align-items: center;
              .icon {
                width: 40px;
                height: 40px;
                @include center;
                border-radius: 50%;
                box-sizing: border-box;
                margin-right: 20px;
                .iconfont {
                  font-size: 25px;
                  color: #fff;
                }
              }
            }
            .text {
              flex-grow: 1;
            }
            .weixin {
              background-color: #22ac38;
              padding-top: 5px;
            }
            .taobao {
              background-color: #73befb;
            }
          }
        }
      }
    }
  }
  &::after {
    content: "";
    clear: both;
  }
}
</style>


